/**
 * filter by multiple words (AND operation)
 */
app.filter('and', ['$filter', function ($filter) {
	'use strict';
	return function (items, searchString) {
		var searchValues = searchString.split(' ');
		var filtered = items;
		for(var i in searchValues) {
			filtered = $filter('filter')(filtered, searchValues[i]);
		}
		return filtered;
	};
}]);

/**
 * group notes by (sub) category
 */
app.filter('groupNotes', ['$filter', function () {
	'use strict';
	return _.memoize(function (notes, category) {
		if(category) {
			// TODO don't use memoize => category-item einfach hinzufügen
			var items = [];
			var prevCat = null;
			for(var i=0; i<notes.length; i+=1) {
				var note = notes[i];
				if(prevCat !== null && prevCat !== note.category) {
					items.push({ isCategory: true, title: note.category.substring(category.length+1) });
				}
				prevCat = note.category;
				items.push(note);
			}
			return items;
		} else {
			return notes;
		}
	});
}]);

app.filter('wordCount', function () {
	'use strict';
	return function (value) {
		if (value && (typeof value === 'string')) {
			var wordCount = value.split(/\s+/).filter(
				// only count words containing
				// at least one alphanumeric character
				function(value) {
					return value.search(/[A-Za-z0-9]/) !== -1;
				}
			).length;
			return window.n('notes', '%n word', '%n words', wordCount);
		} else {
			return 0;
		}
	};
});

